// app/api/listings/route.ts
import { auth } from "@/lib/auth";
import { prisma } from "@/lib/db";
import { NextResponse } from "next/server";

// GET /api/listings?mine=1  → 내 매물 목록
export async function GET(req: Request) {
  const session = await auth();
  const url = new URL(req.url);
  const mine = url.searchParams.get("mine") === "1";

  if (mine) {
    if (!session?.user?.id) {
      return NextResponse.json({ ok: false, error: "unauthorized" }, { status: 401 });
    }
    const items = await prisma.listing.findMany({
      where: { ownerId: session.user.id },
      orderBy: { createdAt: "desc" },
    });
    return NextResponse.json({ ok: true, items });
  }

  // (확장용) 전체 목록 등
  const items = await prisma.listing.findMany({ orderBy: { createdAt: "desc" } });
  return NextResponse.json({ ok: true, items });
}

// POST /api/listings  → 매물 생성(로그인 필요, 기본 CLIENT)
export async function POST(req: Request) {
  const session = await auth();
  if (!session?.user?.id) {
    return NextResponse.json({ ok: false, error: "unauthorized" }, { status: 401 });
  }

  const body = await req.json();
  const {
    title,
    category,
    address_area,
    deposit,
    rent_monthly,
    goodwill_price,
    monthly_sales,
    parking_spaces,
    lat,
    lng,
    photos,
  } = body ?? {};

  if (!title) {
    return NextResponse.json({ ok: false, error: "title is required" }, { status: 400 });
  }

  const created = await prisma.listing.create({
    data: {
      title,
      category: category ?? null,
      address_area: address_area ?? null,
      deposit: deposit ?? null,
      rent_monthly: rent_monthly ?? null,
      goodwill_price: goodwill_price ?? null,
      monthly_sales: monthly_sales ?? null,
      parking_spaces: parking_spaces ?? null,
      lat: lat ?? null,
      lng: lng ?? null,
      photos: photos ?? null,
      ownerId: session.user.id,
    },
    select: { id: true },
  });

  return NextResponse.json({ ok: true, id: created.id });
}
